* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.choose-form {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.choose-form > input {
  height: 40px;
}

.choose-form > button {
  width: 80px;
  height: 30px;
  cursor: pointer;
  border: none;
  border-radius: 10%;
}

.choose-form > button:hover, .choose-form > button:focus {
  border: 1px solid #000;
}

.timer {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.field {
  width: 50px;
}

.field .value {
  font-size: 50px;
}

